import React from 'react'
import DreamEntry from '../components/DreamEntry'
import axios from 'axios'



export default class Dashboard extends React.Component {

  constructor(props) {
    super(props)
    // console.log(props.history)

    this.state = {
      dreamLogFromDb: []
    }
    console.log('dreamLogFromDb===>>>' + this.state.dreamRoute);

    this.listDreamEntry = this.listDreamEntry.bind(this)
  }

  componentDidMount() {
    axios.get(`${process.env.REACT_APP_API_URL}/dreamRoute`)
      .then(res => {
        this.setState({ dreamLogFromDb: [...res.data] })
        console.log('Success getting dreams from db===>>>', this.state.dreamLogFromDb)
      })
      .catch(err => {
        console.log('Error retrieving dreams from db===>>>', err, this.res)
      })
  }

  listDreamEntry() {
    return this.state.dreamLogFromDb.map((data, i) => {
      return <DreamEntry obj={data} key={i} name={this.state.dreamLogFromDb.dreamText} />
    })
  }



  render() {
    return (
      <div>

        <div className="container">
          <table>
            <thead>
              <tr>
                <td>Dream</td>
                <td>Date</td>
                <td>Day of Week</td>
              </tr>
            </thead>
            <tbody>
              {this.listDreamEntry()}
            </tbody>
          </table>
        </div>


        {/* <div className='container'>
          <div className="spacer has-text-centered">
            <h1 className='title'>Your Dashboard</h1>
            <h2>Review your dream log.</h2>
          </div>
        </div>

        <section className="container">

          <table className="table is-striped is-narrow is-hoverable is-fullwidth">
            <thead>
              <tr className="table-header">
                <th>Dream Name</th>
                <th><abbr title="Position">Date</abbr></th>
                <th>Day of Week</th>
                <th><abbr title="Position">Feeling</abbr></th>
                <th>Time to Bed</th>
                <th>Time Awake </th>
              </tr>
            </thead>

            <DreamEntry
              name="Wicked Dreams"
              date="November 18, 2019"
              day="Monday"
              mood="🤔"
              toSleep="10:30 pm"
              wakeUp="7:30 am"
            />

            <DreamEntry
              name="Rainy Saturday"
              date="November 23, 2019"
              day="Monday"
              mood="😊"
              toSleep="10:00 pm"
              wakeUp="9:15 am"
            />

          </table>


        </section> */}

      </div>
    )
  }
}